<template>
  <div class="about fade-in">
    <span class="title fade-in">About Me</span>
    <div class="desc fade-in">
      <span>
        I’m Jamie Moyes, a software engineer and web developer based in Glasgow.
        I’m currently in my fourth year at Strathclyde, studying a masters
        course in Computer Science.
      </span>
      <span>
        When I'm not coding websites and mobile apps I enjoy graphic design and
        making music!
      </span>
    </div>
    <div class="contact fade-in">
      <span>Get in touch!</span>
      <a href="https://www.linkedin.com/in/jamiemoyes" target="_blank"
        >LinkedIn <i class="material-icons">arrow_forward</i></a
      >
      <a href="mailto:jamiecmoyes@gmail.com"
        >Mail <i class="material-icons">arrow_forward</i></a
      >
    </div>
  </div>
</template>

<script>
import { TimelineMax } from "gsap";

export default {
  name: "About",
  mounted() {
    const tl = new TimelineMax();

    const faders = document.querySelectorAll(".fade-in");
    const sliders = document.querySelectorAll(".slide-in");

    const appearOptions = {};

    const appearOnScroll = new window.IntersectionObserver(
      (entries, appearOnScroll) => {
        entries.forEach((e) => {
          if (!e.isIntersecting) return;
          tl.to(e.target, {
            y: -50,
            opacity: 1,
            duration: 0.4,
          });
          appearOnScroll.unobserve(e.target);
        });
      },
      appearOptions
    );

    const slideInOnScroll = new window.IntersectionObserver(
      (entries, appearOnScroll) => {
        entries.forEach((e) => {
          if (!e.isIntersecting) return;
          tl.to(e.target, {
            x: 0,
            opacity: 1,
            duration: 0.6,
          });
          appearOnScroll.unobserve(e.target);
        });
      },
      appearOptions
    );
  
    faders.forEach((e) => appearOnScroll.observe(e));
    sliders.forEach(e => slideInOnScroll.observe(e));
  },
};
</script>

<style scoped>
.about {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-content: center;
  padding: 0 15vw;
  min-height: 100vh;
}

.title {
  font-family: "Asul";
  font-weight: 700;
  font-size: 2em;
  /* width: 6em; */
  padding-bottom: 1em;
}

.desc {
  font-family: "Montserrat";
  text-align: justify;
  font-size: 0.9em;
  max-width: 25em;
  /* min-width: 20em; */
  flex-shrink: 2;
  line-height: 1.7em;
}

.desc span {
  display: block;
  margin-bottom: 1em;
}

.contact {
  /* width: 20em; */
  text-align: left;

  /* padding: 0 5em; */
}

.contact span {
  font-family: "Asul";
  font-weight: 700;
  font-size: 2em;
}

.contact a {
  text-decoration: none;
  color: white;
  font-family: "Montserrat";
  display: block;
  padding-top: 1em;
}

.contact a:hover > i {
  margin-left: 2em;
}

i {
  transition: all 0.4s ease;
  font-size: 1em;
  margin-left: 1em;
}

.fade-in {
  opacity: 0;
}

@media only screen and (max-width: 600px) {
  .desc {
    font-size: 1em;
  }
}
</style>
